
import api from '../api'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
  name:'login',
  data:function(){
    return {
      email:'',
      password:''
    }
  },
  methods:{
    submit_login:function(){
      var self=this;
      if(this.email==""){
        this.$vux.toast.show({
          text:'',
          type:"warn"
        })
      }else if(this.password==""){
         this.$vux.toast.show({
          text:'',
          type:"warn"
        })
      };
      api.user_login({
        email:self.email,
        password:self.password
      }).then(res=>{
        console.log(res);
      })
    }
  },
  components:{
    Group,
    XInput,
    XButton
  }
}



